import axios from "axios";
import React, { useState } from 'react';
import './Addtask.css';



const Addtask = () => {
  const [formData,setFormData] = useState({
    taskname:"",
    taskduration:"",
    taskstatus:"",

  });

  const handleFormSubmit =async(e)=>{
    let response = await axios.post('http://localhost:4000/posts', formData);

    if (response) {
      alert("data submitted");
    }
    else{
      alert("somthing went wrong");
    }

    setFormData({
      taskname:"",
      taskduration:"",
      taskstatus:"",
    });
  };

  return (
<div className='container'>
        <div className='row'>
            <div className='col-md-7'>
                <h1>Add Task</h1>
    <form>
  <div class="mb-3">
    <label for="exampleInputText" className='form-label'>Task Name</label>
    <input type="Text" className='form-control' id="exampleInputText" 
    value={formData.taskname}
    onChange={(e)=> setFormData({... formData,taskname: e.target.value})}
    />
  </div>
  <div class="mb-3">
    <label for="exampleInputText1" className='form-label'>Task Duration</label>
    <input type="Text" className='form-control' id="exampleInputText1"
       value={formData.taskduration}onChange={(e)=> setFormData({... formData,taskduration: e.target.value})}/>
  </div>
  <div class="mb-3">
    <label for="exampleInputText2" className='form-label'>Task Status</label>
    <input type="Text" className='form-control' id="exampleInputText2"value={formData.taskstatus}onChange={(e)=> setFormData({... formData,taskstatus: e.target.value})}/>
  </div>
  <button type="submit" class="btn btn-primary" onClick={handleFormSubmit}>Add Task</button>
</form>
            </div>
        </div>
     </div>
  
    
  )
}

export default Addtask
