import axios from 'axios';
import React, { useEffect, useState } from 'react'

const Tasklist = () => {
    const [data, setData] = useState([{}]);
    useEffect(()=>{
        getUser();
        console.log(data);
    },[]);
    const getUser =async()=>{
        await axios.get("http://localhost:4000/posts").then((res)=>setData(res.data));
    };
    const handleDelete = async(id)=>{
      await axios.delete("http://localhost:4000/posts/"+id).then((res)=>alert("deleted success"));
      getUser();
    }
  return (
    <div>
      <h1>All List of Task</h1>
      <table class="table table-hover table-dark">
  <thead>
    <tr>
      <th scope="col">S.N.</th>
      <th scope="col">Task Name</th>
      <th scope="col">Task Duration</th>
      <th scope="col">Task Status</th>
      <th scope="col">Actions</th>
    </tr>
  </thead>
  <tbody>
    {data && 
    data.map((user)=>(
        <tr>
        <th scope="row">{user.id}</th>
        <td>{user.taskname}</td>
        <td>{user.taskduration}</td>
        <td>{user.taskstatus}</td>
        <td
        style={{display:"flex",justifyContent:"center"}}>
        <button className='btn btn-danger' onClick={()=>handleDelete(user.id)}>Delete</button>
        </td>
      </tr>
    ))}
   
  </tbody>
</table>
    </div>
  )
}

export default Tasklist
